require('dotenv').config()

const test = require('ava')
const _ = require('lodash')

const models = require('../../../infra/sequelize/models')

// test.beforeEach(x => )

test.beforeEach(function truncateDatabase (done) {
  // http://docs.sequelizejs.com/en/v3/docs/raw-queries/
  // https://www.postgresql.org/docs/9.3/static/sql-truncate.html
  // DEV: PostgreSQL doesn't support truncating all tables via a `*`
  // DEV: Our query is vulnerable to SQL injection but we can't use bind and trust our table names more/less
  var tableNames = _.pluck(_.values(models), 'users')
  models('TRUNCATE TABLE ' + tableNames.join(', ')).asCallback(done)
})

test('create user', async t => {
  const userTest = { name: 'vinitest', email: 'emailtest@gmail.com', password: '123' }
  const newUser = await models.user.build(userTest)
  const result = await newUser.save()

  t.is(result.dataValues.name, 'vinitest')
})
